.materials {
    padding: 30px 0;
    color: $dark-grey;
    @include AvenirNext-Bold(28, 1.2);

    // custom styles for materials component on the home-long page
    &_home-long {
        @include imgAbove();
        padding-top: 20px;
    }

    &__title {
        margin-bottom: 60px;
        font-size: 28px;
        text-align: center;

        &_big {
            font-size: 44px;
        }

        &_underline {

            span {
                position: relative;

                &::after {
                    content: '';
                    position: absolute;
                    bottom: -2px;
                    left: 0;
                    width: 100%;
                    height: 2px;
                    background-color: $sunflower-yellow;
                  }
            }
        }
    }

    &__list {
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;

        &_2 {
            justify-content: space-between;
        }

        &_4 {
            justify-content: space-between;
        }
    }

    &__item {
        margin-top: 60px;

        &_2 {
            width: 49%;
        }

        &_3 {
            width: 32%;
            margin-right: 2%;

            &:nth-child(3n) {
                margin-right: 0;
            }

            &:first-child,
            &:nth-child(2),
            &:nth-child(3) {
                margin-top: 0;
            }
        }

        &_4 {
            width: 23%;
            margin-top: 0;

            .materials__item-desc {
                font-size: 20px;
            }
        }

        .materials__title {
            display: none;
        }
    }

    &__item-img {
        height: 215px;
        margin-bottom: 25px;
        overflow: hidden;

        img {
            height: 100%;
            width: auto;
        }
    }

    &__item-title {
        margin-bottom: 15px;
        font-size: 22px;
        line-height: 1.3;
    }

    &__item-desc {
        @include AvenirNext-Regular(18px, 1.4);
        opacity: 0.7;
    }
}

@media (max-width: $lg) {
    .materials {

        // custom styles for materials component on the home-long page
        &_home-long {
            padding-top: 40px;
        }
    
        &__title {
            margin-bottom: 40px;
            font-size: 24px;
        }

        &__item {
            margin-top: 30px;

            &_4 {
                margin-top: 0;

                .materials__item-desc {
                    font-size: 16px;
                }
            }
        }

        &__item-title {
            margin-bottom: 10px;
            font-size: 18px;
            line-height: 1.3;
        }
    
        &__item-desc {
            font-size: 16px;
            line-height: 1.2;
        }
    }
}

@media (max-width: $md) {
    .materials {
        padding: 25px 0;
    
        &__title {
            margin-bottom: 30px;
        }

        &__item-img {
            height: auto;
            margin-bottom: 10px;

            & img {
                width: 100%;
                height: auto;
            }
        }
    }
}

@media (max-width: $sm) {
    .materials {
        padding: 15px 0;
        $this: &;

        // custom styles for materials component on the home-long page
        &_home-long {
            padding-top: 50px;

            #{$this}__item-img {
                margin-bottom: 7px;
            }

            #{$this}__item-title {
                padding-left: 0;
                width: 92%;
                background: none;
            }
        }

        &_mobile-hidden {
            display: none;
        }
    
        &__title {
            margin-bottom: 25px;
            font-size: 18px;

            &_underline {
                margin-bottom: 15px;
                text-transform: uppercase;
            }

            &_outer {
                margin-bottom: 45px;
                text-transform: uppercase;

                span {
    
                    &::after {
                        display: none;
                      }
                }
            }

            &_bonus {

                span {
                    position: relative;

                    &::before {
                        content: '';
                        position: absolute;
                        bottom: -30px;
                        left: 0;
                        width: 100%;
                        height: 25px;
                        background: url('../../img/sales-pages/pricing-bonuses-arrow.svg') no-repeat center;
                        background-size: contain;
                    }
                }
            }
        }

        &__item {
            width: 100%;
            margin-right: 0;
            margin-top: 0;
            margin-bottom: 25px;

            &:last-child {
                margin-bottom: 0;
            }

            .materials__title {
                display: block;
            }
        }

        &__item-img {
            margin-bottom: 15px;
            text-align: center;
        }

        &__item-title {
            width: 66%;
            margin: 0 auto;
            padding-left: 23px;
            font-size: 16px;
            line-height: 1.2;
            background-image: url("../img/sales-pages/icons/checkmark.svg");
            background-repeat: no-repeat;
            position: 0 0;
        }
    
        &__item-desc {
            display: none;
        }
    }
}